@charset "UTF-8";
@font-face {
  font-family: "Main";
  src: url("~@/src/assets/Futura LT Heavy.otf"); }

html,
body {
  font-size: calc(100vw/375*100);
  height: 100%; }

@media screen and (min-width: 600px) {
  html {
    font-size: 160px; } }

@media screen and (min-width: 750px) {
  html {
    font-size: 195px; } }

#root {
  min-height: 100%;
  position: relative;
  overflow: hidden;
  padding-bottom: 0.8rem;
  box-sizing: border-box; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0; }
  to {
    opacity: 1; } }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* Don't kill focus outline for keyboard users: http://24ways.org/2009/dont-lose-your-focus */
a:hover,
a:active {
  outline: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/*这里定义整个应用加载时的动画，进场前这里设置了缩放为0，
进场过程中缩放变为1，也就是100%，持续时间为500ms，
时间曲是ease-out，一种先快后慢的变化曲线*/
.appAppear-appear {
  opacity: 0;
  transform: scale(0); }

.appAppear-appear.appAppear-appear-active {
  opacity: 1;
  transform: scale(1);
  transition: 500ms ease-out; }

/*设置进场前透明度为0，放缩为0*/
.fade-enter {
  opacity: 0; }

/*设置进场过程中 透明度为1，放缩为1，放缩的位置为左上角，持续时间为500ms，时间曲是ease-out */
.fade-enter-active {
  opacity: 1;
  transition: all 500ms ease-out; }

/*设置退场前透明度为1，放缩为1*/
.fade-exit {
  opacity: 1; }

/*设置退场过程中 透明度为0，放缩为0，放缩的位置默认是中间，所以不设置也可以，
  持续时间为500ms，时间曲是ease-out */
.fade-exit-active {
  position: absolute;
  z-index: -1;
  opacity: 0;
  transition: all 0ms ease-out; }

.container {
  position: relative;
  width: 3.75rem;
  margin: 0.25rem auto 0 auto; }
  .container .dinosaur {
    width: 100%;
    height: auto;
    display: block;
    transform: scale(1.2) translateX(0.2rem) translateY(0.1rem); }
  .container .word-1 {
    position: absolute;
    right: 0%;
    top: 45%;
    width: 2.5rem;
    height: 1.5rem;
    background: url("../assets/word-1.png") no-repeat;
    background-size: 100% auto;
    z-index: 2;
    opacity: 0.9; }
  .container .word-2 {
    position: absolute;
    right: 20%;
    top: 65%;
    width: 1.4rem;
    height: 1.5rem;
    background: url("../assets/word-2.png") no-repeat;
    background-size: 100% auto;
    z-index: 2;
    opacity: 0.9; }
  .container .circle {
    position: absolute;
    left: 0.8rem;
    bottom: 0.1rem;
    width: 1.5rem;
    height: 1.5rem;
    background: linear-gradient(rgba(0, 20, 179, 0.6), transparent);
    border-radius: 50%;
    z-index: 1; }
  .container .word-group {
    position: absolute;
    top: 80%;
    width: 100%;
    transform: translateY(-50%);
    font-size: 0.1rem;
    z-index: 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: #fff; }
    .container .word-group span:last-child {
      color: #c09c00; }

.mainBlock {
  width: 3.5rem;
  margin: 0.5rem auto 0.1rem auto;
  display: flex;
  justify-content: center;
  align-items: center; }
  .mainBlock .item {
    width: 1.1rem;
    margin: 0 0.05rem;
    cursor: pointer;
    transition: all .3s;
    box-sizing: border-box;
    overflow: hidden;
    filter: grayscale(90%);
    border-radius: 0.15rem;
    box-shadow: 0 0.05rem 0.05rem #d8d8d8, 0 -0.03rem 0.05rem rgba(216, 216, 216, 0.5); }
    .mainBlock .item:hover {
      filter: grayscale(0%);
      transform: scale(1.01); }
    .mainBlock .item:last-of-type {
      margin-right: 0; }
    .mainBlock .item:first-of-type {
      margin-left: 0; }
    .mainBlock .item .item-icon {
      width: 100%;
      height: 100%;
      flex: 0 1 auto;
      height: auto;
      display: block;
      border-radius: 0.15rem;
      overflow: hidden; }

.main-article {
  width: 3.75rem;
  margin: 0 auto;
  padding: 15px;
  box-sizing: border-box;
  border-radius: 0.15rem;
  overflow: hidden; }
  .main-article .article-title {
    background: #24292e;
    color: #fff;
    font-size: 16px;
    line-height: 20px;
    padding: 20px;
    font-weight: bold;
    border-radius: 10px; }
  .main-article .article-lists .article-item {
    margin-top: 15px;
    margin-bottom: 0; }

.bottom-text {
  width: 100%;
  padding: 0.8rem 0.5rem 0.5rem 0.5rem;
  font-size: 12px;
  text-align: center;
  color: #666;
  box-sizing: border-box; }
